	module.exports = {"data":{"markdownRemark":{"html":"<p>To clone a remote git repository, enter the following into the terminal:</p>\n<blockquote>\n<p>Note: Make sure you are in a root folder e.g. <code>webdev</code> instead of a project specific folder.</p>\n</blockquote>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>git clone <remote_repo>\ncd <remote_repo></code></pre>\n      </div>\n<p>List your branches using these commands:  </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>git branch // Lists local branches\ngit branch -a // Lists local and remote branches</code></pre>\n      </div>\n<p>To checkout a remote branch locally:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>git checkout <branch></code></pre>\n      </div>\n<p>Here is an example of fetching the remote <code>master</code> branch from FreeCodeCamp:  </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>git clone https://github.com/FreeCodeCamp/FreeCodeCamp.git\ncd FreeCodeCamp\ngit checkout master</code></pre>\n      </div>","fields":{"slug":"/miscellaneous/cloning-all-remote-branches-in-git/"},"frontmatter":{"title":"Cloning All Remote Branches in Git"}}},"pathContext":{"slug":"/miscellaneous/cloning-all-remote-branches-in-git/"}}

